import React from "react"
import {
  Button,
  MenuItem,
  Menu,
  ListItemText,
  IconButton,
  Avatar,
  Link,
  Box,
} from "@material-ui/core"
import ArrowDropDown from "@material-ui/icons/ArrowDropDown"
import MailIcon from "@material-ui/icons/Mail"
import NotificationsIcon from "@material-ui/icons/Notifications"
import avatar from "../assets/avatar.png"

export default function NavLinks() {
  const [profileOpen, setProfileOpen] = React.useState(null)
  const [profile, setProfile] = React.useState(null)

  const handleProfileMenuOpen = event => {
    setProfileOpen(event.currentTarget)
  }

  const handleProfileMenuClose = event => {
    setProfileOpen(null)
  }
  const handleProfile = event => {
    setProfile(event.currentTarget)
  }

  const handleProfileMenu = event => {
    setProfile(null)
  }

  return (
    <Box display="flex" pl={4} pr={2}>
      <Button color="inherit">
        <MailIcon />
        Login
      </Button>
      <Button
        aria-label="dropdown"
        color="inherit"
        onClick={handleProfileMenuOpen}
      >
        DropDown
        <ArrowDropDown />
      </Button>
      <Menu
        elevation={0}
        variant="scrollable"
        scrollButtons="on"
        getContentAnchorEl={null}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        id="menu"
        anchorEl={profileOpen}
        keepMounted
        open={Boolean(profileOpen)}
        onClose={handleProfileMenuClose}
      >
        <MenuItem>
          <ListItemText primary="Sent" />
        </MenuItem>
        <MenuItem>
          <ListItemText primary="Drafts" />
        </MenuItem>
        <MenuItem>
          <ListItemText primary="Inbox" />
        </MenuItem>
      </Menu>
      <Button color="inherit">
        <Link color="inherit" href="https://github.com/unicef/unicef-material-ui">
          Github
        </Link>
      </Button>
      <IconButton
        edge="end"
        aria-label="Account of current user"
        aria-haspopup="true"
        onClick={handleProfile}
      >
        <Avatar alt="User" src={avatar} />
      </IconButton>
      <Menu
        elevation={0}
        variant="scrollable"
        scrollButtons="on"
        getContentAnchorEl={null}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        id="menu"
        anchorEl={profile}
        keepMounted
        open={Boolean(profile)}
        onClose={handleProfileMenu}
      >
        <MenuItem>
          <ListItemText primary="Sent mail" />
        </MenuItem>
        <MenuItem>
          <ListItemText primary="Drafts" />
        </MenuItem>
        <MenuItem>
          <ListItemText primary="Inbox" />
        </MenuItem>
      </Menu>
    </Box>
  )
}
